<template>
  <div>
    <bread-nav :nav="nav"></bread-nav>
    <main-part class="main">
      <search-box :searchData="searchData" :table="table"></search-box>
      <div class="btns">
        <div class="btn-main" @click="ClickDateLine">导出</div>
      </div>

      <resize-table>
        <table class="table">
          <thead>
          <tr>
            <th width="6%">部门</th>
            <th width="6%">姓名</th>
            <th width="5%">性别</th>
            <th width="7%">项目名称</th>
            <th width="12%">类型</th>
            <th width="9%">颁奖单位</th>
            <th width="15%">获奖等级及取得的成绩</th>
            <th width="9%">获得时间</th>
            <th width="9%">审核结果</th>
            <th width="9%">评分</th>
            <th width="17%">操作</th>
          </tr>
          </thead>
          <tbody>
          <tr v-for="(item,key) in table.list">
            <td>{{item.organizations[0].name}}</td>
            <td>{{item.teacherName}}</td>
            <td>{{$enums.Sex.getName(item.sex)}}</td>
            <td>{{item.entryName}}</td>
            <td>{{$enums.ProjectType.getName(item.entryType)}}</td>
            <td>{{item.awardUnit}}</td>
            <td>{{item.achievement}}</td>
            <td>{{item.obtainTime | substrTime}}</td>
            <td>
              <span style="margin-right: 2px">{{$enums.ExamineType.getName(item.examineResult)}}</span>
              <el-tooltip effect="dark" :content="item.reason" placement="right"
                          v-if="item.examineResult === 'NOT_PASS'">
                <i class="el-icon-warning"></i>
              </el-tooltip>
            </td>
            <td style="padding:0 3px">
              <span v-if="item.examineResult === 'ADOPT'&&item.status!==true">{{item.score}}</span>
              <el-input placeholder="输入评分"
                        size="small"
                        v-model="formScore.score"
                        v-if="item.examineResult === 'ADOPT' && item.status===true"></el-input>
            </td>
            <td>
              <a class="oper" :href="item.fileUrl" v-if="item.status!==true" @click="onViewClick(item)">下载证明材料</a>
              <span class="oper" @click="review(item)" v-if="item.examineResult === 'UNAUDITED'">审核</span>
              <span class="oper"
                    v-if="item.examineResult === 'ADOPT' && item.status!==true"
                    @click="onSwitch(item,key)">评分</span>
              <span @click="onSubmitScore(item,key)" class="oper" v-if="item.status===true">提交评分</span>
              <span @click="cancelScore(item,key)" class="oper" v-if="item.status===true">取消</span>
            </td>
          </tr></tbody>
        </table>
      </resize-table>
      <no-data v-show="!table.noData"></no-data>
      <paging :data.sync="table"></paging>
      <!--审核-->
      <el-dialog title="审核"
                 :visible.sync="dialogVisible"
                 width="40%"
                 center>
        <el-form label-width="150px">
          <el-form-item label="审核结果：">
            <el-select v-model="form.examineType" placeholder="请选择" style="width: 70%" @change="onFailSwitchChange">
              <el-option v-for="(item, idx) in Review"
                         :key="idx"
                         :label="item.label"
                         :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="原因：" v-if="failSwitch">
            <el-input type="textarea" :autosize="{ minRows: 3, maxRows: 6}"
                      placeholder="请输入原因"
                      v-model="form.reason" style="width: 70%">
            </el-input>
          </el-form-item>
        </el-form>
        <div style="text-align: center">
          <div class="btn-submit" @click="onSubmitStatus">确定</div>
          <div class="btn-clear" style="margin-left: 20px" @click="dialogVisible = false">取消</div>
        </div>
      </el-dialog>

      <!--证明材料-->
      <el-dialog title="证明材料"
                 :visible.sync="annexDialogVisible"
                 width="30%">
        <div v-for="(annexItem,annexIdx) in annexList" class="annex">
          <a :href="annexItem.fileUrl" target="_blank">点击下载证明材料{{annexIdx+1}}</a>
        </div>
        <div style="text-align: center;margin-top: 20px">
          <div class="btn-submit" @click="annexDialogVisible = false">返回</div>
        </div>
      </el-dialog>
    </main-part>
  </div>
</template>
<script>
  import mixins from '../../../assets/js/mixins'

  export default {
    mixins: [mixins.$exportDetail],
    data () {
      return {
        nav: [{name: '教研成果', path: ''}],
        dialogDate: [],
        searchData: [
          {type: 'input', placeholder: '项目名称'},
          {type: 'select', title: '类型', model: 'entryType', option: this.$enums.ProjectType.allList()},
          {
            type: 'dateSection', title: '时间',
            startDate: new Date().setTime(new Date().getTime() - 3600 * 1000 * 24 * 90), endDate: new Date(),
            modelName: ['startDate', 'endDate']
          },
        ],
        table: {
          url: '/teachingResearch/queryTeachingResearchPage.do',
          el: '.table',
          total: 0,
          noData: true,
          list: [],
          query: {
            keywords: null,
            entryType: null,
            startDate: new Date(new Date().setMonth(new Date().getMonth() - 6)).$Format('yyyy-MM-dd' + ' 00:00:00'),
            endDate: new Date().$Format('yyyy-MM-dd hh:mm:ss')
          }
        },
        dialogVisible: false,
        title: null,
        switch: false,
        switchOne: true,
        failSwitch: false,
        // 提交审核状态
        form: {
          id: null,
          examineType: null,
          reason: null
        },
        // 提交评分
        formScore: {
          id: '',
          score: ''
        },
        // 证明材料
        annexDialogVisible: false,
        annexList: [],

        // 审核
        Review: this.$enums.ExamineType.list
      }
    },
    watch: {
      dialogVisible () {
        if (!this.dialogVisible) {
          this.failSwitch = false
          this.form.examineType = null
        }
        if (this.dialogVisible) {
          if (this.Review.length < 3) return
          this.Review.shift()
        }
      }
    },
    methods: {
      // 处理时间--导出
      ClickDateLine () {
        let newDate = this.table.query.startDate
        let newDateEndDate = this.table.query.endDate
        this.table.query.startDate = this.table.query.startDate.substr(0, 10)
        this.table.query.startDate = this.table.query.startDate.replace(/(\d{4})-(\d{2})-(\d{2})/g, '$1/$2/$3')
        this.table.query.endDate = this.table.query.endDate.substr(0, 10)
        this.table.query.endDate = this.table.query.endDate.replace(/(\d{4})-(\d{2})-(\d{2})/g, '$1/$2/$3')
        this.$exportDetail('/export/teachingResearchExport.do')
        this.table.query.startDate = newDate
        this.table.query.endDate = newDateEndDate
      },
      // 证明材料
      onViewClick (item) {
        this.annexDialogVisible = true
        this.annexList = item.attachments
      },
      // 展开编辑表单
      onSwitch (item, key) {
        this.formScore = {
          id: item.id,
          score: item.score
        }
        item.status = true
        this.table.list.splice(key, 1, item)
      },
      // 审核
      review (item) {
        this.dialogVisible = true
        this.form.id = item.id
      },
      // 审核不通过时展开原因
      onFailSwitchChange (val) {
        val === 'NOT_PASS' ? this.failSwitch = true : this.failSwitch = false
      },
      // 取消评分
      cancelScore (item, key) {
        item.status = false
        this.table.list.splice(key, 1, item)
      },
      // 提交评分
      async onSubmitScore (item) {
        if (Number(this.formScore.score) < Number('1')) {
          this.$message.warning('评分不能小于1哦')
          return
        }
        await this.$ajax({
          url: '/teachingResearch/scoreTeachingResearch.do',
          el: '.table',
          query: this.formScore
        })
        this.$remind('评分成功')
        this.$search(this.table)
      },
      // 提交审核结果
      async onSubmitStatus () {
        try {
          await this.$ajax({
            url: '/teachingResearch/examineTeachingResearch.do',
            query: this.form
          })
          this.$remind('设置成功')
          this.dialogVisible = false
          this.$search(this.table)
          this.form.id = null
          this.form.examineType = null
          this.form.reason = null
        } catch (e) {
          this.form.id = null
          this.form.examineType = null
          this.form.reason = null
        }
      }
    },
    async mounted () {
      this.$search(this.table)
    }
  }
</script>
<style lang="sass" scoped>
  .main
    .btns
      position: absolute
      right: 15px
      top: 6px
      z-index: 9
    .annex
      margin-bottom: 10px
      a
        color: $main-c
        text-decoration: underline
    /deep/ .el-dialog
      width: 35% !important
      border-radius: 10px 10px 0 0
      .el-dialog__header
        background-color: $main-c
        border-radius: 10px 10px 0 0
        .el-dialog__title
          color: #ffffff
        .el-dialog__close
          color: #ffffff
      .dialog_title
        padding: 10px 0
        margin-bottom: 30px
        border-top: 1px solid $border
        border-bottom: 1px solid $border
        text-align: center
        font-size: $font-large
        font-weight: 600
</style>
